import React, { useState, useEffect } from 'react'
import styled from 'styled-components'
import { useLocation } from 'react-router-dom';
import { useCookies } from 'react-cookie';
import PageSection from 'components/PageSection'
import useTheme from 'hooks/useTheme'
import { PageMeta } from 'components/Layout/Page'
import { GetAddress, cookieExpirationDate } from 'utils/vipReflinks';
import Hero from './components/Hero'
import CakeDataRow from './components/CakeDataRow'

const StyledHeroSection = styled(PageSection)`
  padding-top: 16px;

  ${({ theme }) => theme.mediaQueries.md} {
    padding-top: 48px;
  }
`

const Home: React.FC = () => {
  const [location] = useState(useLocation());
  const { theme } = useTheme();
  const [cookies, setCookie] = useCookies(['Refaddress', 'Refselection']);

  useEffect(() => {
    const reflink = location.pathname.slice(1);
    const vIPAddress = GetAddress(reflink);
    if (vIPAddress !== undefined) {
      setCookie('Refaddress', vIPAddress, { path: '/', expires: cookieExpirationDate })
      setCookie('Refselection', "two", { path: '/', expires: cookieExpirationDate })
    }
  }, [location, setCookie]);

  return (
    <>
      <PageMeta />
      <StyledHeroSection
        innerProps={{ style: { margin: '0', width: '100%', lineHeight: 0 } }}
        background={
          theme.isDark
            ? 'radial-gradient(103.12% 50% at 50% 50%, #304FAC 0%, #0d162b 100%)'
            : 'linear-gradient(139.73deg, #E6FDFF 0%, #F3EFFF 100%)'
        }
        index={2}
        hasCurvedDivider={false}
      >
        <Hero />
        <CakeDataRow />
      </StyledHeroSection>
    </>
  )
}

export default Home
